  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    if (!this.checkDataFormChange(this.valueChange, this.frmData) || !this.checkStudentChange(this.jsonStudents, this.studentSelect)) {
      return new Observable<boolean>(
        (observer) => {
          this.confirmService.confirm().subscribe(
            value => {
              switch (value) {
                case 'yes':
                  this.subData('GHI_TAM').then(value1 => {
                    observer.next(true);
                    observer.complete();
                  });
                  break;
                case 'no':
                  observer.next(true);
                  observer.complete();
                  break;
                case 'cancel':
                  observer.next(false);
                  observer.complete();
                  break;
                default:
                  observer.next(true);
                  observer.complete();
                  break;
              }
            }
          );
        });
    } else {
      return true;
    }
  }

