  async delete(data) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.hoadonbanra);
    if (!isCheckInitFee)
      return;
    if (this.selectedItems.length || data) {
      const status = '';
      let ids = '';
      let batch_id = '';
      if (data) {
        ids = data.id;
        batch_id = data.batch_id;
      } else {
        ids = this.selectedRows.join(',');
        batch_id = this.selectedItems[0].batch_id;
      }
      const object = {
        'invoiceIds': ids,
        'batchId': batch_id,
      };
      const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.vatService.deleteInvoiceWithBatch(object).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify(this._translate.NOTIFY.delete_success, 'success');
              if (rs.data == "delete_batch")
                this.loadListBatch();
              else {
                this.loadListBatch();
                if (this.productGrip) {
                  this.productGrip.instance.collapseAll(-1);
                  this.productGrip.instance.expandRow(batch_id);
                }
                this.loadList(batch_id);
              }

            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    }
  }

