  guibenmua(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    let check = true;
    if (data) {
      if (data.status !== 'DA_XUAT') {
        check = false;
      }
    } else {
      if (this.selectedItems.filter(p => p.status === 'DA_XUAT').length === 0) {
        check = false;
      }
    }

    if (check && (this.selectedItems.length || data)) {
      Library.showloading();
      let batch_id = this.selectedItems[0].batch_id;
      let ids = [];
      if (data) {
        ids.push(data.id);
      } else {
        ids = this.selectedItems.filter(p => p.status === 'DA_XUAT').map(p => p.id);
      }
      this.vatService.layCacHoaDonKySoHangLoat(ids, this.loaiHoaDon).subscribe((rs: any) => {
        if (rs[0].status === 'success') {
          const initialState = {
            invoiceList: rs[0].data.invoices,
            configs: rs[1].data.configs,
            isReceipts: this.isReceipts
          };
          this.bsModalSignRef = this.modalService.show(SendListEmailComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg full-modal',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            if (this.bsModalSignRef.content.successSaveItem) {
              this.selectedItems = [];
              this.selectedRows = [];
              this.loadList(batch_id);
              subModal.unsubscribe();
            }
          });
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      Library.notify(this._translate.NOTIFY.no_invoice_selected, 'error');
    }
  }

