import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

import { pattern } from '@config/index';
import { Helper, Library,AuthService } from '@core/index';
import { VatService } from '@modules/vat/shared';

@Component({
  selector: 'app-send-mail',
  templateUrl: './send-mail.component.html',
  styleUrls: ['./send-mail.component.scss']
})
export class SendMailComponent implements OnInit {

  invoice_id: any;
  frmData: any = {};
  email: string;
  pattern = pattern;
  _translate: any;

  constructor(
    public bsModalRef: BsModalRef,
    private vatService: VatService,
    private authService: AuthService,
  ) { 
    this._translate = this.authService.getTranslate();

  }

  ngOnInit() {
    this.frmData.email = this.email;
  }

  onSubmit(e) {
    let data = [{
      id: this.invoice_id,
      email: this.frmData.email
    }];
    Library.showloading();
    let sub = this.vatService.sendMail(data).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify(this._translate.NOTIFY.send_mail_success, 'success');
        this.bsModalRef.content.successSaveItem = "success_mail";
        this.bsModalRef.hide();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {}, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

}
