  onRowRemoving(e) {
    const student_id = e.data.student_id;
    const indexRemove = [];
    this.dataSource.forEach(row => {
      if (row.student_id === student_id) {
        indexRemove.push(this.dataSource.indexOf(row));
      }
    });
    for (let i = indexRemove.length - 1; i >= 0; i--) {
      this.dataSource.splice(indexRemove[i], 1);
    }
    this.checkStatusData();
    let _index = 0;
    let _index_view_order = 0;
    this.dataSource.forEach(item => {
      item.order = ++_index;
      item.view_order = ++_index_view_order;
    });
    this.dataGrid.instance.refresh();
    this.onStudentChange.emit(null);
  }

