  onCellPrepared(e) {
    if (e.rowType === 'data') {
      if (e.data.statusData === 'OTHER_REDUCE') {
        this.setStatusDataOnCellPrepared(e, this._translate.NOTIFY.different_reduce, false);
      }
      if (e.data.statusData === 'ERROR_DUPLICATE') {
        this.setStatusDataOnCellPrepared(e, this._translate.NOTIFY.invoice_duplicate, true);
      }

      if (e.data.number_of_vouchers) {
        switch (e.column.dataField) {
          case 'buyer_address':
            if (e.data.customer_object_code === false) {
              this.setStatusDataOnCellPrepared(e, 'a ch khng c  trng', false);
            }
            break;
          default:
            break;
        }
      } else if (e.data.number_of_vouchers === '' && e.data.dataType === 'KHACH_HANG' && e.data.product_name && e.column.dataField === 'number_of_vouchers') {
        this.setStatusDataOnCellPrepared(e, 'S phiu, chng t khng c  trng', false);
      }
      if (e.column.command === "edit") {
        if (e.row.data.invoice_number) {
          let tempt = e.cellElement.children;
          tempt[0].outerHTML = "";
        }
      }
    }
  }

  async setStatusDataOnCellPrepared(e, message, isDuplicate) {
    if (!e.value) {
      if (!isDuplicate) {
        await e.row.cells.forEach(item => {
          if (item.cellElement) {
            // item.cellElement.style.background = 'red url(\'assets/img/icons/fa-remove.png\') no-repeat 2% center';
            item.cellElement.style.background = 'red';
            item.cellElement.title = message;
          }
        });
      } else {
        await e.row.cells.forEach(item => {
          if (item.cellElement) {
            item.cellElement.style.background = 'yellow';
            item.cellElement.title = message;
          }
        });
      }
    } else if (e.value && e.data.statusData === 'DATA_ERROR') {
      e.data.statusData = 'INVALID';
    }
  }
  view(data) {
    if (data && data.invoice_number) {
      let ids = [];
      if (data) {
        ids.push(data.id);
      }
      let w = 1920, h = 900;
      w = window.screen.availWidth * 100 / 100;
      h = window.screen.availHeight * 93 / 100;
      let win = window.open('about:blank', 'name', 'width=200, height=100');
      win.document.body.innerHTML = this._translate.NOTIFY.loading;
      win.blur();
      Library.showloading();
      let sub = this.vatService.xemHoaDonInvoiceForSeries(ids).subscribe(rs => {
        if (rs.status == 'success') {
          let pdf_file = rs.data.invoice.pdf_file;
          let objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
          win.resizeTo(screen.availWidth, screen.availHeight);
          win.focus();
          win.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
        } else {
          win.close();
          Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
