import { AfterViewInit, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DxDataGridComponent } from 'devextreme-angular';
import CustomStore from 'devextreme/data/custom_store';
import * as XLSX from 'xlsx';
import { AuthService, Helper, Library, VChanggeMoneyService, VStringService } from '@core/index';
import { AddComponent, ConfigDecimalComponent } from '@shared/index';
import { InvoiceModel, VatService } from '@modules/vat/shared';
import { ProductsService } from '@modules/products/shared';
import { TranslateService } from '@ngx-translate/core';
import { sEnum } from '@config/sEnum';
import { SelectMonthComponent } from '@shared/components/select-month/select-month.component';
import { InvoiceService } from '@shared/service/invoice.service';

declare var $: any;


@Component({
  selector: 'app-product-grip',
  templateUrl: './product-grip.component.html',
  styleUrls: ['./product-grip.component.scss']
})
export class ProductGripComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  routeSub: Subscription;

  @Input() dataSource: any;
  @Input() invoice_products: any;
  @Input() formatter: any;
  @Input() isHdGTGT: any;
  @Input() _objTemplateSelect: any;
  @Input() fee_months: any;
  @Input() student_reduce_rate_code: any;
  @Input() extentionGrid: any;
  @Input() formDataInInvoice: any; // Khng t tnh gi tr
  @Input() arrProducts = [];
  @Input() studentSelect: any;
  @Input() _oTemplateTT78: any;
  @Input() isTT78: boolean;
  @Output('onCheckDuplicate') onCheckDuplicate = new EventEmitter<any>();
  select: any = {};
  _translate: any;
  frmData: InvoiceModel = new InvoiceModel(1);
  bsModalRef: BsModalRef;
  showPopup = false; // Popup
  dataExcelFile: any = null;
  dataExcelFileInfo: any = null;
  ttDaXuatHoaDon = false;
  configs: any;
  amount_after_vat: any = 0;
  reloadSelectProduct = true;
  extras = [];
  rowData = 1;
  showmenu = false;
  selectMonth = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private modalService: BsModalService,
    private vatService: VatService,
    private productsService: ProductsService,
    private authService: AuthService,
    public translate: TranslateService,
    private invoiceService: InvoiceService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.import_from_excel,
      items: [
        {
          id: 'LAY_MAU_BANG_KE',
          name: this._translate.CONTROLL.BUTTON.get_the_sample_file,
          icon: this._translate.ICONS.download
        },
        {
          id: 'NAP_DU_LIEU_BANG_KE',
          name: this._translate.CONTROLL.BUTTON.import_data,
          icon: this._translate.ICONS.excel
        }
      ]
    }];
  }

  ngAfterViewInit() {
    // let a = moment(new Date()).format('HH:mm:ss.SSS');
  }

  ngOnInit() {

    if (!this.isHdGTGT) {
      this.formDataInInvoice.amount_vatx = null;
      this.formDataInInvoice.amount_vat5 = null;
      this.formDataInInvoice.amount_vat10 = null;
    }
    this.configs = this.authService.getConfigs();
    let test = this._objTemplateSelect;
    // let a = moment(new Date()).format('HH:mm:ss.SSS');
    this.routeSub = this.route.data.subscribe((data: any) => {
      // put data DM for select
      let danh_muc = this.authService.getDMLocal();
      this.select.unit_codes = danh_muc.DM_DON_VI_TINH ? danh_muc.DM_DON_VI_TINH.data : null;
      this.select.select_provisions_on_accounts = danh_muc.DM_DINH_KHOAN ? danh_muc.DM_DINH_KHOAN.data : null;
      this.select.vats = danh_muc.DM_TY_LE_VAT ? danh_muc.DM_TY_LE_VAT.data : null;
      this.select.revenue_forms = danh_muc.DM_HINH_THUC_THU ? danh_muc.DM_HINH_THUC_THU.data : null;
      this.select.countrys = danh_muc.DM_QUOC_GIA ? danh_muc.DM_QUOC_GIA.data : null;
      // Hng ha dch v
      let isEdit = false;
      let invoice: any;
      if (data.init[2] == true)
        isEdit = true;
      if (this.isTT78) {
        this.select.products = data.init[0];
        if (isEdit)
          invoice = data.init[1];
        this.select.productsView = {
          store: new CustomStore({
            key: 'code',
            loadMode: 'raw',
            load: (loadOptions) => {
              return this.select.products;
            },
            byKey: function (key) {
              return data.init[0].filter(p => p.code === key.toString());
            }
          }),
          paginate: true,
          sort: 'name'
        };
      }
      else {
        this.select.products = data.init[1];
        if (isEdit)
          invoice = data.init[3];
        this.select.productsView = {
          store: new CustomStore({
            key: 'code',
            loadMode: 'raw',
            load: (loadOptions) => {
              return this.select.products;
            },
            byKey: function (key) {
              const _data = data.init[1].filter(p => p.code === key.toString());
              return _data;
            }
          }),
          paginate: true,
          sort: 'name'
        };
      }
      this.select.products.forEach(item => {
        item.mhvth = item.code + ': ' + item.name;
      });
      this.invoice_products.load().then((dataSource) => {
        dataSource.forEach((item, index) => {
          if (item.product_name && item.unit_name) {
            const obj1 = this.select.unit_codes.filter(p => p.name === item.unit_name)[0];
            if (!obj1) {
              if (VStringService._getValueToString(item.unit_code) == "")
                item.unit_code = VStringService._convertToViKdau(item.unit_name);
              let oDuplicate = this.select.unit_codes.filter(p => p.code === item.unit_code)[0];
              if (oDuplicate)
                item.unit_code = item.unit_code + Helper.addtionalUnitCode();
              this.select.unit_codes.push({ code: item.unit_code, name: item.unit_name });
            }
            else {
              if (VStringService._getValueToString(item.unit_code) != "") {
                let oExistUnitCode = this.select.unit_codes.filter(p => p.code === item.unit_code)[0];
                if (!oExistUnitCode)
                  this.select.unit_codes.push({ code: item.unit_code, name: item.unit_name });
              }
            }
          }
        });
      });
    });
    // Event
    this.createUpdateEvent();
  }

  // Thm gi tr mi vo n v tnh
  lookupColumnOptions = {
    acceptCustomValue: true,
    showClearButton: true,
    onCustomItemCreating: function (args) {
      //
      //
      const newItem: any = {};
      newItem.code = VStringService._convertToViKdau(args.text);
      let oDuplicate = this.select.unit_codes.filter(p => p.code === newItem.code)[0];
      if (oDuplicate)
        newItem.code = newItem.code + Helper.addtionalUnitCode();
      newItem.name = args.text;
      this.select.unit_codes.unshift(newItem);

      setTimeout(() => {
        this.dataGrid.instance.columnOption('unit_code', 'lookup', {
          dataSource: this.select.unit_codes,
          displayExpr: 'name',
          valueExpr: 'code'
        });
      });

      return newItem;
    }.bind(this)
  };

  // khi to s kin khi update
  createUpdateEvent() {
    setTimeout(() => {
      this.invoice_products.on('updated', (key, value) => {
        const column = Object.keys(value)[0];
        if (value.product_code !== 'add') {
          this.invoice_products.byKey(key).then((row) => {
            switch (column) {
              case 'view_order':
                this.view_orderChange();
                break;
              case 'product_code':
                this.product_codeChange(row);
                break;
              case 'product_name':
                this.product_nameChange(row);
                break;
              case 'unit_code':
                this.unit_codeChange(row);
                break;
              case 'quantity':
              case 'price':
              case 'price_nte':
              case 'vat':
              case 'amount':
              case 'amount_vat':
              case 'amount_after_vat':
              case 'amount_discount':
              case 'discount':
              case 'amount_without_discount':
              case 'other_tax':
              case 'other_charge':
                this._tinhGiaTri();
                break;
              default:
                break;
              // }
            }
          }, (error) => {
            alert(error);
          });
        } else {
          this.invoice_products.remove(key);
          this.addRow();
        }
      });
    }, 100);
  }

  onGripKeyDown(e) {
    if (e.event.code === 'Delete') {
      e.event.srcElement.value = null;
    }
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      e.rowElement.style.whiteSpace = 'pre-wrap';
      const eles = e.rowElement.getElementsByTagName('td');
      if (!e.isEditing) {
        eles[2].innerHTML = e.data.product_code;
      }
      if (e.data.error) {
        const ele = eles[eles.length - 1];
        ele.style.background = 'red url(\'assets/img/icons/fa-remove.png\') no-repeat 2% center';
        ele.title = e.data.error;
      }
    }
  }

  onEditorPreparing(e) {
    if (e.parentType === 'dataRow') {
      if (e.dataField === 'product_code') {
        e.editorOptions.onOpened = function (arg) {
          const popupInstance = arg.component._popup;
          popupInstance.option('width', 400);
          popupInstance.option('minWidth', 400);
        };
      }

      if (e.dataField === 'product_name' || e.dataField === 'school_fee_month') {
        e.editorName = 'dxTextArea';
      }

      // e.editorOptions.onFocusOut = (args) => {
      //   if (e.dataField === 'unit_code') {
      //     const _obj = this.select.unit_codes.filter(p => p.code === e.row.data.unit_code)[0];
      //     if (_obj) {
      //       e.row.data.unit_name = _obj.name;
      //     } else {
      //       e.row.data.unit_name = '';
      //     }
      //   }
      //   if (e.dataField === 'country_code') {
      //     const _obj = this.select.countrys.filter(p => p.code === e.row.data.country_code)[0];
      //     if (_obj) {
      //       e.row.data.country_name = _obj.name;
      //     } else {
      //       e.row.data.country_name = '';
      //     }
      //   }
      //   this.invoice_products.update(e.row.data.id, e.row.data);
      // };
    }
  }


  onEditorPrepared(e) {
    if ((e.dataField === 'amount_without_discount' || e.dataField === 'discount' || e.dataField === 'amount_discount' || e.dataField === 'quantity' || e.dataField === 'price' || e.dataField === 'amount' || e.dataField === 'amount_vat' || e.dataField === 'amount_after_vat') && e.parentType === 'dataRow') {
      e.editorElement.childNodes[1].firstChild.onwheel = (evt) => {
        e.component.cancelEditData();
      };

      e.editorElement.childNodes[1].firstChild.addEventListener('select', function () {
        this.selectionStart = this.selectionEnd;
      }, false);
    }
  }

  onContextMenuPreparing(e) {
    if (e.row.rowType === 'data') {
      e.items = [
        {
          text: this._translate.CONTROLL.BUTTON.add_goods,
          onItemClick: () => {
            this.addProduct(e.row.data);
          }
        },
        {
          text: this._translate.CONTROLL.BUTTON.add_row,
          onItemClick: () => {
            this.addRow();
          }
        },
        {
          text: this._translate.CONTROLL.BUTTON.delete_row,
          onItemClick: () => {
            this.deleteRow(e.row.data);
          }
        },
      ];
    }
  }

  addProduct(row) {
    Library.showloading();
    this.productsService.getAllListbyListtypeByCode('DM_NHOM_HANG_HOA,DM_DON_VI_TINH,DM_TY_LE_VAT').subscribe((response) => {
      const initialState = {
        totalCount: 0,
        groupProducts: response.data['DM_NHOM_HANG_HOA'],
        unitCalculations: this.select.unit_codes,//response.data['DM_DON_VI_TINH'],
        vats: response.data['DM_TY_LE_VAT'],
        groupProductListTypeId: response.data['ids']['DM_NHOM_HANG_HOA'],
        unitCalculationListTypeId: response.data['ids']['DM_DON_VI_TINH'],
        vatListTypeId: response.data['ids']['DM_TY_LE_VAT'],
        wavehourses: [],
        formatter: this.formatter,
        onSaveProduct: (data, unitCodes) => {
          this.select.unit_codes = unitCodes;
          data.mhvth = data.code + ': ' + data.name;
          this.select.products.push(data);
          this.reloadSelectProduct = false;
          setTimeout(() => {
            this.reloadSelectProduct = true;
          }, 50);

          row.product_code = data.code;
          row.product_name = data.name;
          row.group_code = data.group_code;
          row.unit_code = this.checkUnitCode(data.unit_code);
          row.quantity = null;
          row.price = data.price ? parseFloat(data.price) : data.price;
          row.amount = data.price ? parseFloat(data.price) : 0;
          row.vat = data.vat ? data.vat.toString() : null;
          row.amount_vat = (row.vat >= 0) ? this.caculatorAmountVat(row.amount, row.vat) : null;
          row.amount_after_vat = row.amount + row.amount_vat;
          this.unit_codeChange(row);
          this.invoice_products.update(row.id, row);
        }
      };
      this.bsModalRef = this.modalService.show(AddComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false
      });
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  addRow() {
    let len, order, orderLast;
    len = this.dataSource.items().length;
    orderLast = this.dataSource.items()[len - 1].order;
    for (let i = 0; i < this.rowData; i++) {

      if (this.dataSource.items().length >= 1) {
        order = ++orderLast;
      } else {
        order = 1;
      }

      const data = this.frmData.add_invoice_products_data(order, order);
      this.invoice_products.insert(data);
    }
    this.dataSource.reload();
  }

  view_orderChange() {
    let _index = 0;
    if (!this.isTT78) {
      this.dataSource.items().forEach(item => {
        if (item.view_order && VStringService._getValueToString(this._objTemplateSelect.template_type) !== sEnum.templatetype.HoaDonNhaHangKhachSan) {
          item.view_order = ++_index;
        }
        if (VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonNhaHangKhachSan) {
          if (!item.is_money_service) {
            item.view_order = ++_index;
          } else {
            item.view_order = null;
          }
        }
      });
    }
    else {
      this.dataSource.items().forEach(item => {
        if (item.view_order && VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonNhaHangKhachSan) {
          item.view_order = ++_index;
        }
        if (VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonNhaHangKhachSan) {
          if (!item.is_money_service) {
            item.view_order = ++_index;
          } else {
            item.view_order = null;
          }
        }
      });
    }

  }

  // Thay i ct m hng 
  product_codeChange(row) {
    this.select.products.some(item => {
      if (item.code === row.product_code) {
        this.productsService.getProduct(item.id).subscribe(rs => {
          if (rs.status == "success") {
            let productServ = rs.data.product;
            if (productServ) {
              let isUpdate = true;
              let _indexNumber = 0;
              if (isUpdate) {
                if (productServ.form_revenue == sEnum.hinhthucthu.Thang) {
                  this.selectMonth = true;

                  const initialState = {
                    fee_months: this.fee_months,
                    product_code: row.product_code
                  };
                  this.bsModalRef = this.modalService.show(SelectMonthComponent, {
                    class: 'second child-modal',
                    initialState,
                    backdrop: 'static',
                    keyboard: false,
                  });
                  let index = 0;
                  this.modalService.onHide.subscribe(() => {
                    if (this.selectMonth) {
                      index++;
                      if (index == 1) {
                        if (this.bsModalRef.content.successSaveItem) {
                          if (row.product_code == this.bsModalRef.content.product_code) {
                            let _lstMonth = this.bsModalRef.content.successSaveData;
                            _lstMonth = _lstMonth.split(';').filter(p => p != "");
                            row.school_fee_month = this.bsModalRef.content.successSaveData;
                            row.product_name = productServ.name;
                            if (this.select.revenue_forms && this.select.revenue_forms.length > 0) {
                              const _obj = this.select.revenue_forms.filter(p => p.code === productServ.form_revenue)[0];
                              if (_obj) {
                                row.semester = _obj.name;
                              }
                            }
                            row.unit_code = this.checkUnitCode(productServ.unit_code);
                            if (row.unit_code == "NAM" || row.unit_code == "NAMHOC" || row.unit_code == "KY")
                              row.quantity = 1;
                            else
                              if (row.unit_code == "THANG")
                                row.quantity = _lstMonth.length;
                              else
                                row.quantity = null;
                            row.price = productServ.price ? parseFloat(productServ.price) : productServ.price;
                            row.price = row.price ? parseFloat(row.price) - (row.price * parseFloat(this.student_reduce_rate_code)) / 100 : row.price;
                            row.amount = row.price ? parseFloat(row.price) * parseFloat(row.quantity) : row.price;
                            if (this.isHdGTGT) {
                              row.vat = productServ.vat != null ? productServ.vat.toString() : null;
                            } else {
                              row.vat = null;
                            }
                            if (this.isHdGTGT) {
                              row.amount_vat = (row.vat >= 0) ? this.caculatorAmountVat(row.amount, row.vat) : null;
                            } else {
                              row.amount_vat = null;
                            }
                            row.group_code = productServ.group_code;
                            row.amount_after_vat = row.amount + (row.amount_vat != null ? row.amount_vat : 0);
                            this.invoice_products.update(row.id, row);
                            this._tinhGiaTri();
                            let prot = this.arrProducts;
                            if (!this.arrProducts || (this.arrProducts && this.arrProducts.length == 0)) {
                              const IProduct = {
                                code: productServ.code,
                                price: productServ.price
                              };
                              this.arrProducts.push(IProduct);
                            } else {
                              const product = this.arrProducts.filter(p => p.code === productServ.code)[0];
                              if (!product) {
                                const IProduct = {
                                  code: productServ.code,
                                  price: productServ.price
                                };
                                this.arrProducts.push(IProduct);
                              }
                            }
                            if (row.school_fee_month !== '') {
                              this.onCheckDuplicate.emit(null);
                            }
                            return true;
                          }

                        }
                        else {
                          if (row.product_code == this.bsModalRef.content.product_code) {
                            row.school_fee_month = "";
                            row.product_name = productServ.name;
                            row.group_code = productServ.group_code;
                            row.unit_code = this.checkUnitCode(productServ.unit_code);
                            row.quantity = null;
                            if (this.select.revenue_forms && this.select.revenue_forms.length > 0) {
                              const _obj = this.select.revenue_forms.filter(p => p.code === productServ.form_revenue)[0];
                              if (_obj) {
                                row.semester = _obj.name;
                              }
                            }
                            row.price = productServ.price ? parseFloat(productServ.price) : productServ.price;
                            row.price = row.price ? parseFloat(row.price) - (row.price * parseFloat(this.student_reduce_rate_code)) / 100 : row.price;
                            row.amount = row.price ? parseFloat(productServ.price) : row.price;
                            if (this.isHdGTGT) {
                              row.vat = productServ.vat != null ? productServ.vat.toString() : null;
                            } else {
                              row.vat = null;
                            }
                            if (this.isHdGTGT) {
                              row.amount_vat = (row.vat >= 0) ? this.caculatorAmountVat(row.amount, row.vat) : null;
                            } else {
                              row.amount_vat = null;
                            }
                            row.amount_after_vat = row.amount + (row.amount_vat != null ? row.amount_vat : 0);
                            let test = this.invoice_products;
                            this.invoice_products.update(row.id, row);
                            this._tinhGiaTri();
                            if (!this.arrProducts || (this.arrProducts && this.arrProducts.length == 0)) {
                              const IProduct = {
                                code: productServ.code,
                                price: productServ.price
                              };
                              this.arrProducts.push(IProduct);
                            } else {
                              const product = this.arrProducts.filter(p => p.code === productServ.code)[0];
                              if (!product) {
                                const IProduct = {
                                  code: productServ.code,
                                  price: productServ.price
                                };
                                this.arrProducts.push(IProduct);
                              }
                            }
                            if (row.school_fee_month !== '') {
                              this.onCheckDuplicate.emit(null);
                            }
                            return true;
                          }
                        }
                      }
                    }
                  });

                }
                else {
                  row.school_fee_month = '';
                  const _oHinhThucThu = this.select.revenue_forms.filter(p => p.code == productServ.form_revenue)[0];
                  if (_oHinhThucThu) {
                    if (_oHinhThucThu.note) {
                      const mapObj = {
                        T: VStringService._getValueToString(this.fee_months.code).split('-')[0],
                        S: VStringService._getValueToString(this.fee_months.code).split('-')[1],
                      };
                      row.school_fee_month = VStringService._getValueToString(_oHinhThucThu.note).replace(/T|S/gi, function (matched) {
                        return mapObj[matched];
                      });
                    }
                  }
                  let _lstMonth = null;
                  if (row.school_fee_month !== '') {
                    _lstMonth = row.school_fee_month.split(';').filter(p => p !== '');
                  }
                  if (_lstMonth) {
                    row.quantity = _lstMonth.length;
                  } else {
                    row.quantity = null;
                  }
                  if (this.select.revenue_forms && this.select.revenue_forms.length > 0) {
                    const _obj = this.select.revenue_forms.filter(p => p.code === productServ.form_revenue)[0];
                    if (_obj) {
                      row.semester = _obj.name;
                    }
                  }
                  row.product_name = productServ.name;
                  row.group_code = productServ.group_code;
                  row.unit_code = this.checkUnitCode(productServ.unit_code);
                  if (row.unit_code == "NAM" || row.unit_code == "NAMHOC" || row.unit_code == "KY")
                    row.quantity = 1;
                  else
                    if (row.unit_code == "THANG")
                      row.quantity = _lstMonth.length;
                    else
                      row.quantity = null;
                  row.price = productServ.price ? parseFloat(productServ.price) : productServ.price;
                  row.price = row.price ? parseFloat(row.price) - (row.price * parseFloat(this.student_reduce_rate_code)) / 100 : row.price;
                  if (row.quantity) {
                    row.amount = row.price ? parseFloat(row.price) * parseFloat(row.quantity) : row.price;
                  } else {
                    row.amount = row.price ? parseFloat(row.price) : row.price;
                  }
                  if (this.isHdGTGT) {
                    row.vat = productServ.vat != null ? productServ.vat.toString() : null;
                  } else {
                    row.vat = null;
                  }
                  if (this.isHdGTGT) {
                    row.amount_vat = (row.vat >= 0) ? this.caculatorAmountVat(row.amount, row.vat) : null;
                  } else {
                    row.amount_vat = null;
                  }
                  row.amount_after_vat = row.amount + (row.amount_vat != null ? row.amount_vat : 0);
                  this.invoice_products.update(row.id, row);
                  this._tinhGiaTri();
                  if (!this.arrProducts || (this.arrProducts && this.arrProducts.length == 0)) {
                    const IProduct = {
                      code: productServ.code,
                      price: productServ.price
                    };
                    this.arrProducts.push(IProduct);
                  } else {
                    const product = this.arrProducts.filter(p => p.code === productServ.code)[0];
                    if (!product) {
                      const IProduct = {
                        code: productServ.code,
                        price: productServ.price
                      };
                      this.arrProducts.push(IProduct);
                    }
                  }
                  if (row.school_fee_month !== '') {
                    this.onCheckDuplicate.emit(null);
                  }
                  return true;
                }

              } else {
                return false;
              }
            }

          }
          else {
            Library.notify(rs.message, 'error');
          }
        });
      }
    });
  }

  unit_codeChange(row) {
    const _obj = this.select.unit_codes.filter(p => p.code === row.unit_code)[0];
    if (_obj) {
      row.unit_name = _obj.name;
      this.invoice_products.update(row.id, row);
    } else {
      row.unit_name = '';
      this.invoice_products.update(row.id, row);
    }
  }

  product_nameChange(row) {
    this.invoice_products.update(row.id, row);
    this.refreshInvoiceProduct();
  }
  refreshInvoiceProduct() {
    const invoice_products = [];
    this.dataSource.items().forEach((item, index) => {
      if (item.product_code || item.product_name || (!item.product_name && item.is_promotion)) {
        item.row_content = item.product_name;
        invoice_products.push(item);
        if (item.is_transport_number) {
          this.formDataInInvoice.transport_number = item.product_name;
        }
      }
    });
    this.formDataInInvoice.invoice_products = invoice_products;
  }

  checkUnitCode(unitCode) {
    const items = this.select.unit_codes;
    for (let i = 0; i < items.length; i++) {
      if (items[i].code === unitCode) {
        return unitCode;
      }
    }
    return null;
  }

  _tinhGiaTri() {
    if (!this.isTT78) {
      setTimeout(() => {
        VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.formDataInInvoice, this.formatter, !this.formDataInInvoice.auto_value, this.formDataInInvoice.auto_value_price, this.select, this._objTemplateSelect, this.configs.system_parameter);
        this.amount_after_vat = this.formDataInInvoice.amount_after_vat;
      }, 100);
    }
    else {
      setTimeout(() => {
        VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.formDataInInvoice, this.formatter, !this.formDataInInvoice.auto_value, this.formDataInInvoice.auto_value_price, this.select, this._oTemplateTT78, this.configs.system_parameter);
        this.amount_after_vat = this.formDataInInvoice.amount_after_vat;
      }, 100);
    }

  }

  caculatorAmountVat(amount, vat) {
    const temp = amount / 100 * vat;
    return this.roundNumber(temp, 5);
  }

  roundNumber(number, n): number {
    let p = Math.pow(10, n);
    return Math.round(number * p) / p;
  }

  // Bt modal thay i s thp phn
  configDecimal(n) {
    const initialState = {
      column: n,
      formatter: this.formatter,
      showDonGiaNte: this.configs.system_parameter.view_price_nte,
    };
    this.bsModalRef = this.modalService.show(ConfigDecimalComponent, {
      class: 'modal-lg', initialState, backdrop: 'static',
      keyboard: false
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      const data = this.bsModalRef.content.successSaveItem;
      if (data) {
        this._tinhGiaTri();
      }
      subModal.unsubscribe();
    });
  }

  // Xa hng
  deleteRow(item) {
    setTimeout(() => {
      const check = confirm(this._translate.CONFIRM.delete);
      if (check && !this.ttDaXuatHoaDon) {
        setTimeout(() => {
          this.invoice_products.remove(item.id);

          // this.dataSource.store().remove(item.id);
          this.dataSource.reload();
          this._tinhGiaTri();
          const product = this.arrProducts.filter(p => p.code === item.product_code)[0];
          let index = this.arrProducts.indexOf(product);
          this.arrProducts.splice(index, 1);
          this.onCheckDuplicate.emit(null);
        }, 100);
      }
    }, 200);
  }

  updateSomeTotalInput() {
    this._tinhGiaTri();
  }

  // Ly mu bng k
  layMauBangKe() {
    Library.showloading();
    this.vatService.layFileMau({
      customer_name: this.formDataInInvoice.customer_name,
      buyer_tax_code: this.formDataInInvoice.buyer_tax_code,
      buyer_address: this.formDataInInvoice.buyer_address,
      buyer_name: this.formDataInInvoice.buyer_name,
      invoice_type_name: ""
    }).subscribe(rs => {
      if (rs.status === 'success') {
        const file = rs.data.invoice;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  // Np d liu bng k
  napDuLieuTuBangKe() {
    document.getElementById('file-bang-ke').click();
  }

  onFileBangKeChange(evt) {
    const target: DataTransfer = <DataTransfer>(evt.target);
    if (target.files.length !== 1) {
      throw new Error('Khng th chn nhiu file');
    }
    const reader: FileReader = new FileReader();
    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      /* save data */
      const data = <any>(XLSX.utils.sheet_to_json(ws, { header: 'A', range: 10 }));
      const account = <any>(XLSX.utils.sheet_to_json(ws, { header: 'A', range: 0 }));
      const fileBangKe: any = document.getElementById('file-bang-ke');
      fileBangKe.value = '';
      this.dataExcelFile = data;
      this.dataExcelFileInfo = account.slice(0, 4);
      this.showPopup = true;
    };
    reader.readAsBinaryString(target.files[0]);
  }

  thayTheToanBo() {
    this.fillDataFromFile(this.dataExcelFile, this.dataExcelFileInfo, 'thaythe');
    this.showPopup = false;
    this.dataExcelFile = null;
  }

  themTiep() {
    this.fillDataFromFile(this.dataExcelFile, this.dataExcelFileInfo, 'themtiep');
    this.showPopup = false;
    this.dataExcelFile = null;
  }

  huy() {
    this.showPopup = false;
    this.dataExcelFile = null;
  }

  fillDataFromFile(data, info, status) {
    if (info.length === 4) {
      const customer_name = info[0].A.split(':');
      if (customer_name.length > 1) {
        this.formDataInInvoice.customer_name = customer_name[1].trim();
      }
      ////
      const buyer_tax_code = info[1].A.split(':');
      if (buyer_tax_code.length > 1) {
        this.formDataInInvoice.buyer_tax_code = buyer_tax_code[1].trim();
      }
      ////
      const buyer_name = info[2].A.split(':');
      if (buyer_name.length > 1) {
        this.formDataInInvoice.buyer_name = buyer_name[1].trim();
      }
      ////
      const buyer_address = info[3].A.split(':');
      if (buyer_address.length > 1) {
        this.formDataInInvoice.buyer_address = buyer_address[1].trim();
      }
    }
    if (status === 'thaythe') {
      this.dataSource.items().forEach(item => {
        // this.dataSource.store().remove(item.id);
        this.invoice_products.remove(item.id);


      });
    } else {
      this.dataSource.items().forEach(item => {
        if (!item.product_code && !item.product_name && !item.quantity && !item.price && !item.amount && !item.amount_vat && !item.amount_after_vat) {
          this.invoice_products.remove(item.id);

        }
      });
    }
    // let arr = [];
    let j = 10000;
    data.forEach(item => {
      // item.A = item.A || "0";
      item.B = item.B || '';
      item.C = item.C || '';
      item.D = item.D || '';
      // item.E = item.E || '0';
      // item.F = item.F || '0';
      // item.G = item.G || '0';
      // item.H = item.H || "0";
      // item.I = item.I || "0";
      const temp: any = {};
      if (this.formDataInInvoice.auto_value) {
        temp.id = Helper.makeid();
        if (item.A) {
          temp.view_order = parseInt(item.A, 10);
        } else {
          temp.view_order = null;
        }
        temp.product_code = item.B;

        temp.product_name = item.C;
        temp.unit_code = this.getUnitCodeByUnitName(item.D);
        if (item.E) {
          temp.quantity = Helper.toNumber(item.E);
        }
        if (item.F) {
          temp.price = Helper.toNumber(item.F);
        }
        if (item.G) {
          temp.amount = Helper.toNumber(item.G);
        }
        if (item.H && this.isHdGTGT) {
          temp.vat = item.H;
        } else {
          temp.vat = null;
        }
        if (this.isHdGTGT) {
          temp.amount_vat = item.I != null ? Helper.toNumber(item.I) : null;
        } else {
          temp.amount_vat = null;
        }
        temp.amount_after_vat = temp.amount_vat + temp.amount;
      } else {
        temp.id = Helper.makeid();
        // temp.order = (status == 'thaythe') ? parseInt(item.A) : j;
        if (item.A) {
          temp.view_order = parseInt(item.A, 10);
        } else {
          temp.view_order = null;
        }
        temp.product_code = item.B;
        temp.product_name = item.C;
        temp.unit_code = this.getUnitCodeByUnitName(item.D);
        if (item.E) {
          temp.quantity = Helper.toNumber(item.E);
        }
        if (item.F) {
          temp.price = Helper.toNumber(item.F);
        }
        if (item.G) {
          temp.amount = Helper.toNumber(item.G);
        }
        // temp.amount = temp.price * temp.quantity;
        if (item.H && this.isHdGTGT) {
          temp.vat = item.H; // parseInt(item.H);
        } else {
          temp.vat = null;
        }
      }
      const _obj = this.select.products.filter(p => p.code === temp.product_code)[0];
      if (!_obj && temp.product_code !== '') {
        const _objPush = {
          code: temp.product_code,
          name: temp.product_name,
          mhvth: temp.product_code + ': ' + temp.product_name,
        };
        this.select.products.push(_objPush);
      }
      temp.commercial_discount = false;
      // arr.push(temp);
      // this.dataSource.store().insert(temp);
      this.invoice_products.insert(temp);
      j++;
    });
    setTimeout(() => {
      this.dataGrid.instance.columnOption('unit_code', 'lookup', {
        dataSource: this.select.unit_codes,
        displayExpr: 'name',
        valueExpr: 'code'
      });
    });
    this.dataSource.reload();
    this._tinhGiaTri();
  }

  getUnitCodeByUnitName(unitName) {
    if (VStringService._getValueToString(unitName) !== '') {
      if (this.select.unit_codes && this.select.unit_codes.length > 0) {
        const _obj = this.select.unit_codes.filter(p => p.name === unitName)[0];
        if (_obj) {
          return _obj.code;
        } else {
          let code = VStringService._convertToViKdau(unitName);
          let oDuplicate = this.select.unit_codes.filter(p => p.code === code)[0];
          if (oDuplicate)
            code = code + Helper.addtionalUnitCode();
          this.select.unit_codes.push({ code: code, name: unitName });
          return code;
        }
      } else {
        this.select.unit_codes = [];
      }
    }
    return null;
  }

  ngOnDestroy() {
    this.routeSub.unsubscribe();
  }

  itemExtrasClick(e) {
    if (e.itemData.id === 'LAY_MAU_BANG_KE') {
      this.layMauBangKe();
    } else if (e.itemData.id === 'NAP_DU_LIEU_BANG_KE') {
      this.napDuLieuTuBangKe();
    }
  }

  onCellClick(e) {
    if (e.column.dataField === 'school_fee_month') {
      this.selectMonth = false;
      const product = this.select.products.filter(p => p.code === e.row.data.product_code)[0];
      if (product) {
        this.productsService.getProduct(product.id).subscribe(rs => {
          if (rs.status == "success") {
            let productServ = rs.data.product;
            if (productServ) {
              if (productServ.form_revenue === sEnum.hinhthucthu.Thang) {
                const initialState = {
                  fee_months: this.fee_months,
                  month_year: e.row.data.school_fee_month,
                  product_code: e.row.data.product_code
                };
                this.bsModalRef = this.modalService.show(SelectMonthComponent, {
                  class: 'second child-modal',
                  initialState,
                  backdrop: 'static',
                  keyboard: false,
                });
                let index = 0;
                this.modalService.onHide.subscribe(() => {
                  if (!this.selectMonth) {
                    index++;
                    let tr = index;
                    if (index == 1) {
                      if (e.row.data.product_code == this.bsModalRef.content.product_code) {
                        if (this.bsModalRef.content.successSaveItem) {
                          e.row.data.school_fee_month = this.bsModalRef.content.successSaveData;
                        }
                        else {
                        }
                        let _lstMonth = [];
                        if (e.row.data.school_fee_month)
                          _lstMonth = e.row.data.school_fee_month.split(';').filter(p => p !== '');
                        if (_lstMonth && _lstMonth.length > 0) {
                          e.row.data.quantity = _lstMonth.length;
                        } else {
                          e.row.data.quantity = null;
                        }
                        if (e.row.data.quantity) {
                          e.row.data.amount = e.row.data.price ? parseFloat(e.row.data.price) * parseFloat(e.row.data.quantity) : 0;
                        } else {
                          e.row.data.amount = e.row.data.price ? parseFloat(e.row.data.price) : 0;
                        }
                        if (this.isHdGTGT) {
                          e.row.data.amount_vat = (e.row.data.vat >= 0) ? this.caculatorAmountVat(e.row.data.amount, e.row.data.vat) : null;
                        } else {
                          e.row.data.amount_vat = null;
                        }
                        e.row.data.amount_after_vat = e.row.data.amount + (e.row.data.amount_vat != null ? e.row.data.amount_vat : 0);
                        this.invoice_products.update(e.row.data.id, e.row.data);
                        this._tinhGiaTri();
                        if (e.row.data.school_fee_month && e.row.data.school_fee_month !== '') {
                          this.onCheckDuplicate.emit(null);
                        }
                        return true;
                      }
                    }
                  }
                });
              }
            }
          }
          else {
            Library.notify(rs.message, 'error');
          }
        });
      }
    }
  }
  onContentReady = (e) => {
    const arrFooter = [
      { name: this._translate.GRID.INVOICE_DETAILS.total_money, class: 'table-footer-1' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount_vat, class: 'table-footer-2' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount, class: 'table-footer-3' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount_without_discount, class: 'table-footer-4' },
    ]
    const domGrid = document.querySelector('#gridInvoice');
    this.invoiceService.formatSumColumnGrid(arrFooter, domGrid);
  }
}
