import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import * as moment from 'moment';

import { Helper, Library, SocketService, AuthService, VStringService } from '@core/index';
import { VatService } from '@modules/vat/shared';

@Component({
  selector: 'app-sign-many',
  templateUrl: './sign-many.component.html',
  styleUrls: ['./sign-many.component.scss']
})
export class SignManyComponent implements OnInit {

  invoices: any;
  configs: any;
  invoiceList: any;
  ioSignature: any;
  invoiceIndex = 0;
  tempKiFile = 0;
  tempTemplate: any;
  daXong: boolean = false;
  ganXong: boolean = false;
  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  _translate: any;
  nodeSign: any;
  isTT78: boolean = false;
  infoDangKy: any;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }

  constructor(
    public bsModalRef: BsModalRef,
    private vatService: VatService,
    private socketService: SocketService,
    private authService: AuthService,
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    const currentUser = this.authService.getCurrentUser();
    this.infoDangKy = this.authService.getInfoRegister();
    if (this.infoDangKy) {
      if (this.infoDangKy.status == "CHAP_NHAN")
        this.isTT78 = true;
    }
    this.convertData();
    this.connectWebsocket();
  }

  convertData() {
    this.invoiceList.forEach(item => {
      item.invoice_date = moment(item.invoice_date).format('YYYY-MM-DD HH:mm:ss');
      delete item.updated_at;
      delete item.decpoint_is_dot;
      delete item.money_decimal_format;
      item.invoice_products = item.invoice_products.sort((itemA, itemB) => {
        return itemA.order - itemB.order;
      });
    });
  }

  onImportShowing(e) {
    this.valueProgress = 0;
  }

  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm(this._translate.CONFIRM.sign_stop);
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }

  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDonImport().subscribe(message => {
      let data: any = message;
      if (data.Status == 'success') {
        if (data.SvcName == "efy_Sign") {
          let i = this.invoiceIndex;
          if (data.Data.Name == 'hoadon.xml') {
            this.goiApiXuatHoaDon(data.Data.Content, this.invoices[i]);
          } else if (data.Data.Name == 'ten_filek_the_trung-dc-nhi.xml') {
            this.invoices[i].issuer_name = data.Data.IssuerName;
            this.xuLiApiKhiXuat(this.invoices[i]);
          } else {
            this.invoices[i].invoice_files.forEach(item => {
              if (item.file_name == data.Data.Name) {
                item.file_content = data.Data.Content;
              }
            });
            this.tempKiFile++;
            if (this.tempKiFile == this.invoices[i].invoice_files.length) {
              this.xuLiApiKhiXuat(this.invoices[i]);
            }
          }
        }
      } else {
        this.showPopup = false;
        this.errorImport = true;
        Library.notify(data.Description, 'error');
      }
    });
  }

  xuLyKiFile(xmlInvoice, signDate) {
    if (this.isTT78) {
      let ListSerialNumber = "DSCKS";
      if (this.infoDangKy && this.infoDangKy.registration_signature) {
        let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
        if (signatures && signatures.length > 0)
          ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
      }
      this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', this.nodeSign, signDate, 'InvoiceData', ListSerialNumber, true);
    }
    else
      this.socketService.kiSo(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', this.nodeSign, signDate);
  }

  xuLiApiKhiXuat(invoice) {
    let tempTemplate = invoice.template;
    delete invoice.template;
    let xmlInvoice = "";
    let isNewXml = 0;
    if (!this.isTT78) {
      let sub = this.vatService.edit(invoice.id, invoice, true).subscribe(rs => {
        invoice.template = tempTemplate;
        if (rs.status == 'success') {
          invoice.invoice_date = moment(rs.data.invoice_date).format('YYYY-MM-DD HH:mm:ss');

          //demo k s vo file
          //  Ly xml t serer
          isNewXml = rs.data.is_new_xml;
          if (isNewXml)
            this.nodeSign = "//HDon/DSCKS/NBan";
          else
            this.nodeSign = "//inv:transaction/inv:invoices/inv:invoice";
          xmlInvoice = VStringService._getValueToString(rs.data.xml_string);
          if (xmlInvoice != "") {
            if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
              || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
              this.goiApiXuatHoaDon(xmlInvoice, invoice);
            } else {
              this.xuLyKiFile(xmlInvoice, invoice.invoice_date);
            }
          }
        } else {
          this.invoiceList[this.invoiceIndex].statusByMe = rs.message;
          this.showPopup = false;
          this.errorImport = true;
          //this.onImportHiding();
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        sub.unsubscribe();
      });
    }
    else {
      let sub = this.vatService.editTT78(invoice.id, invoice, true).subscribe(rs => {
        invoice.template = tempTemplate;
        if (rs.status == 'success') {
          invoice.invoice_date = moment(rs.data.invoice_date).format('YYYY-MM-DD HH:mm:ss');
          let invoice_sign_date = moment(rs.data.invoice_sign_date).format('YYYY-MM-DD HH:mm:ss');
          //demo k s vo file
          //  Ly xml t serer
          isNewXml = rs.data.is_new_xml;
          if (isNewXml)
            this.nodeSign = "//HDon/DSCKS/NBan";
          else
            this.nodeSign = "//inv:transaction/inv:invoices/inv:invoice";
          xmlInvoice = VStringService._getValueToString(rs.data.xml_string);
          if (xmlInvoice != "") {
            if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
              || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
              this.goiApiXuatHoaDon(xmlInvoice, invoice);
            } else {
              this.xuLyKiFile(xmlInvoice, invoice_sign_date);
            }
          }
        } else {
          this.invoiceList[this.invoiceIndex].statusByMe = rs.message;
          this.showPopup = false;
          this.errorImport = true;
          //this.onImportHiding();
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        sub.unsubscribe();
      });
    }

  }

  goiApiXuatHoaDon(xml_string, invoice) {
    let percentComplete = 100 / this.totalInvoice;
    // Helper.dowloadFile('hoadon.xml', 'xml', xml_string);
    this.subXuatHoaDon = this.vatService.xuatHoaDon({
      id: invoice.id,
      xml_string: xml_string,
      invoice_files: invoice.invoice_files
    }).subscribe(rs => {
      if (rs.status == 'success') {
        this.valueProgress += percentComplete;
        this.invoiceList[this.invoiceIndex].statusByMe = 'DA_KY';
        this.ganXong = true;
        this.tempKiFile = 0;

        if (this.invoiceIndex < this.totalInvoice - 1) {
          this.invoiceIndex++;
          this.xuatHoaDon();
        } else if (this.invoiceIndex == this.totalInvoice - 1) {
          Library.notify(this._translate.NOTIFY.sign_success, 'success');
          this.daXong = true;
          setTimeout(() => {
            this.showPopup = false;
          }, 300);
        }
      } else {
        this.invoiceList[this.invoiceIndex].statusByMe = rs.message;
        this.showPopup = false;
        this.errorImport = true;
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      // Library.hideloading();
      this.subXuatHoaDon.unsubscribe();
    });
  }

  xuatHoaDon() {
    let i = this.invoiceIndex;
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.xuLiApiKhiXuat(this.invoices[i]);
    } else {
      if (this.invoices[i].invoice_files.length) {
        this.invoices[i].invoice_files.forEach(item => {
          this.socketService.kiFile(() => this.connectWebsocket(), item.file_content, item.file_name, item.file_name.split('.').pop(), true, this.invoices[i].invoice_date);
        });
      }
      else {
        if (this.isTT78) {
          let ListSerialNumber = "DSCKS";
          if (this.infoDangKy && this.infoDangKy.registration_signature) {
            let signatures = this.infoDangKy.registration_signature.filter(p => p.register_form == "THEM_MOI" || p.register_form == "GIA_HAN");
            if (signatures && signatures.length > 0)
              ListSerialNumber = signatures.map((obj: any) => obj.cert_serial).join(';');
          }
          let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
          this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", ListSerialNumber, true);
        }
        else
          this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice')
      }
    }
  }

  kyHangLoat() {
    Library.confirmVA(this._translate.CONFIRM.sign_inv, this._translate.NOTIFY.notify, () => {
      this.showPopup = true;
      this.invoices = JSON.parse(JSON.stringify(this.invoiceList.filter(item => item.statusByMe != 'DA_KY')));
      console.log(this.invoices);
      this.totalInvoice = this.invoices.length;
      this.invoiceIndex = 0;
      this.tempKiFile = 0;
      this.xuatHoaDon();
    });
  }

  dong() {
    if (this.ganXong) {
      this.bsModalRef.content.successSaveItem = "success";
    }
    this.bsModalRef.hide()
  }


}