  setDataListStudentandClass(dataStudent) {
    // list class student
    this.classStudentList = this.returnAllStudentinStudentFillter(dataStudent);
    // list class
    this.dataClass = dataStudent.reduce((previousValue, currentValue) => {
      if (checkItemDuplicate(currentValue.code)) {
        return previousValue;
      } else {
        return previousValue.concat(
          {
            name: currentValue.name,
            code: currentValue.code,
            checked: false
          }
        );
      }

      function checkItemDuplicate(item) {
        return previousValue.some(result => {
          return result.code === item;
        });
      }
    }, []);
    // this.countCheckedStudent(this.studentList);
  }

  returnAllStudentinStudentFillter(dataArrOrigin) {
    return dataArrOrigin.reduce((previousValue, currentValue) => {
      if (checkItemDuplicate(currentValue.code)) {
        return previousValue;
      } else {
        return previousValue.concat(
          {
            id: currentValue.id,
            name: currentValue.name,
            code: currentValue.code,
            checked: false,
            isShow: false,
            childrenList: []
          }
        );
      }

      function checkItemDuplicate(item) {
        return previousValue.some(result => {
          return result.code === item;
        });
      }
    }, []);
  }

  returnStudentSearch(dataStudent) {
    const dataArr = dataStudent.reduce((previousValue, currentValue) => {
      if (checkItemDuplicate(currentValue.class_code)) {
        return previousValue;
      } else {
        return previousValue.concat(
          {
            name: currentValue.class_name,
            code: currentValue.class_code,
            checked: false,
            isShow: true,
            childrenList: []
          }
        );
      }

      function checkItemDuplicate(item) {
        return previousValue.some(result => {
          return result.code === item;
        });
      }
    }, []);
    dataStudent.forEach(item => {
      dataArr.forEach(itemClassList => {
        if (item.class_code === itemClassList.code) {
          itemClassList['checked'] = false;
          itemClassList.childrenList.push(item);
        }
      });
    });
    return dataArr;
  }

  // event emit ra khi chn lp
