  async getListClass($event) {
    let nodes = $event.component.getNodes();
    let nodes_temp = nodes.filter(node => node.selected == true)
    this.valueFilterClass = _.map(nodes_temp, 'key')
    this.getClassCode();
  }

  // // filter student to TLMG
