  sortArr(previousValue, currentValue) {
    const bandA = previousValue.code.toUpperCase();
    const bandB = currentValue.code.toUpperCase();

    let comparison = 0;
    if (bandA > bandB) {
      comparison = 1;
    } else if (bandA < bandB) {
      comparison = -1;
    }
    return comparison;
  }

