  async addStudent() {
    let dataCount = 1;
    await this.studentService.getCountStudent().then(result => {
      if (result.status === 'success') {
        dataCount = result.data.meta.total + 1;
      }
    });
    if (this.productGrip) {
      this.productGrip.instance.collapseAll(-1);
    }
    const initialState = {
      // totalCount: this.dataGrid.instance.totalCount(),
      classCodes: this.arrClass,
      student_reduce_rate_codes: this.dataTLMG,
      totalCount: dataCount,
      classCodeListTypeId: this.getIdDmLop(),
    };
    this.bsModalRef = this.modalService.show(AddStudentComponent, {
      class: 'modal-lg full-modal',
      initialState,
      backdrop: 'static',
      keyboard: false,
    });

    this.modalService.onHide.subscribe(() => {
      this.studentService.getAllClass()
        .then(result => {
          this.dm.DM_LOP_HOC.data = result.data.DM_LOP_HOC;
          localStorage.setItem('DANH_MUC', JSON.stringify(this.dm));
          this.dataClass = _.map(result.data.DM_LOP_HOC, (item: any) => ({
            code: item.code,
            name: item.name,
            checked: false
          }));
          this.dataClass = this.dataClass.sort(this.sortArr);
          this.arrClass = this.arrClass.sort(this.sortArr);
        })
      if (this.bsModalRef.content.successSaveItem) {
        if (this.productGrip) {
          this.productGrip.instance.collapseAll(-1);
        }
      }
    });
  }



  editStudent() {
    if (this.selectedItems.length === 1) {
      const initialState = {
        student: this.selectedItems[0],
        classCodes: this.arrClass,
        student_reduce_rate_codes: this.dataTLMG,
        classCodeListTypeId: this.getIdDmLop(),
      };
      this.bsModalRef = this.modalService.show(AddStudentComponent, {
        class: 'modal-lg full-modal',
        initialState,
        backdrop: 'static',
        keyboard: false,
      });
      this.modalService.onHide.subscribe(() => {
        this.studentService.getAllClass()
          .then(result => {
            this.dm.DM_LOP_HOC.data = result.data.DM_LOP_HOC;
            localStorage.setItem('DANH_MUC', JSON.stringify(this.dm));
            this.dataClass = _.map(result.data.DM_LOP_HOC, (item: any) => ({
              code: item.code,
              name: item.name,
              checked: false
            }))
          })
        if (this.bsModalRef.content.successSaveItem) {
          if (this.productGrip) {
            this.productGrip.instance.collapseAll(-1);
          }
        }
      });

    } else if (this.selectedItems.length > 1) {
      Library.notify(this._translate.NOTIFY.only_seclected_student_to_edit, 'error');
      return;
    } else {
      Library.notify(this._translate.NOTIFY.no_selected_student, 'error');
      return;
    }
  }

  deleteStudent() {
    if (this.selectedItems.length > 0) {
      const ids = this.selectedItems.map((obj: any) => obj.id).join(',');
      const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
      if (result) {
        result.then((dialogResult) => {
          if (dialogResult) {
            Library.showloading();
            this.studentService.deleteStudent(ids).subscribe((response: any) => {
              if (response.status === 'success') {
                Library.notify(this._translate.NOTIFY.delete_success, 'success');
                if (this.productGrip) {
                  this.productGrip.instance.collapseAll(-1);
                }
                // if (this.isFillter) {
                //   this.isFillter = false;
                //   this.setDataListStudentandClass(this.arrClass);
                // } else {
                //   this.setDataListStudentandClass(this.arrClass);
                // }
              } else {
                Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
              }
              Library.hideloading();
            });
          }
        });
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_selected_student, 'error');
      return;
    }
  }

