  async searchKeyWord() {
    await this.studentService.getClassBySearch(null, this.valueFilterClass, this.valueFilterTLMG, this.keySearch).then(
      result => {
        this.arrClass = [];
        if (result.status === 'success' && result.data.meta.total > 0) {
          if (result.data.students.length > 0) {
            let temp_filterClass = _.uniq(_.map(result.data.students, 'class_code'));            
            this.arrClass = _.map(_.filter(this.dataClass, item => temp_filterClass.includes(item.code)), ({code, name}) => ({code, name}))            
            if (this.productGrip) {
              this.productGrip.instance.collapseAll(-1);
              this.productGrip.instance.expandRow(this.arrClass[0]);
              this.loadListDataItem(this.arrClass[0].code);
              
            }
          }
          return true;
        } else {
          Library.notify(this._translate.NOTIFY.not_find_students, 'error');
          return false;
        }
      }
    );

  }

