  onCellClickClass(e) {
    if (e.rowType == "data") {
      e.component.collapseAll(-1);
      if(!e.row.isExpanded) {
        e.component.expandRow(e.key);
        this.loadListDataItem(e.row.data.code);
      }
    }
  }
  loadListDataItem(classCode) {
    this.dataSource = [];
    this.getStudentToClass(classCode).then(value => {
      if (value.status === 'success' && value.data.meta.total > 0) {
        this.dataSource = value.data.students;
        return true;
      } else {
        return false;
      }
    });
  }
  selectionChanged(data: any) {
    if (this.selectedItems.length == 0)
      this.classClick = 'animaltion';
    this.selectedItems = data.selectedRowsData;
    // if (this.selectedItems.length > 0) {
    //   this.isRender = true;
    // }
    // else
    //   this.isRender = false;
  }
  getClassCode() {
    if(this.valueFilterClass.length > 0){
      this.arrClass = _.map(_.filter(this.dataClass, item => this.valueFilterClass.includes(item.code)), ({code, name}) => ({code, name}))
    }else {
      this.arrClass = _.map(this.dataClass, ({code, name}) => ({code, name}))
    }
  }
  groupBy(list, keyGetter) {
    const map = new Map();
    list.forEach((item) => {
      const key = keyGetter(item);
      const collection = map.get(key);
      if (!collection) {
        map.set(key, [item]);
      } else {
        collection.push(item);
      }
    });
    return map;
  }
  getIdDmLop() {
    let classCodeListTypeId = '';
    const lstType = this.authService.getListtypes();
    const dm_lop = lstType.find(item => item.code == "DM_LOP_HOC");
    if (dm_lop)
      classCodeListTypeId = dm_lop.id;
    return classCodeListTypeId;
  }
