import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { IndexComponent } from '@modules/student-invoice/components/index/index.component';
import { InvoiceComponent } from '@modules/student-invoice/components/invoice/invoice.component';
import { InvoiceResolver, ShowInvoiceResolver } from './shared';
import { VatDeactivateGuard } from '@modules/vat/deactivate/vat-deactivate.guard';
const routes: Routes = [
  { path: '', component: IndexComponent },
  {
    path: 'create',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: InvoiceResolver },
    data: { brec: 'create_invoice' }
  },
  {
    path: 'create-receipts',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: InvoiceResolver },
    data: { brec: 'create_receipts', type: "08HSPT" }
  },
  {
    path: 'edit',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ShowInvoiceResolver },
    data: { brec: 'invoice_details' }
  },
  {
    path: 'edit-receipts',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ShowInvoiceResolver },
    data: { brec: 'receipts_details', type: "08HSPT" }
  },
  {
    path: ':invoiceId',
    component: InvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ShowInvoiceResolver },
    data: { brec: 'invoice_details' }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StudentInvoiceRouting {
}
