import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map, catchError } from 'rxjs/operators';
import { ListsService } from '@modules/lists/shared';
import { StudentInvoiceService } from './student-invoice.service';
import { Library, Helper, AuthService, sEnum } from '@core/index';
import { VatService } from '@modules/vat/shared';
import { forkJoin } from 'rxjs';

@Injectable()
export class InvoiceResolver implements Resolve<any[]> {
  constructor(
    private vatService: VatService,
    private router: Router,
    private listsService: ListsService,
    private authService: AuthService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
    Library.showloading();
    let isTT78 = false;
    let infoDangKy = this.authService.getInfoRegister();
    if (infoDangKy && infoDangKy.id) {
      if (infoDangKy.status == "CHAP_NHAN")
        isTT78 = true;
    }
    let loaiHoaDon = route.data.type;
    if (loaiHoaDon == sEnum.loaihoadon.HSPT)
      isTT78 = true;
    let _countDm = 0;
    let dataJoin = [];
    let dataDM: any;
    let arr = [
      {
        "code": "DM_NHOM_KHACH_HANG",
        "order": 0
      },
      {
        "code": "DM_LOAI_TIEN",
        "order": 0
      },
      {
        "code": "DM_HINH_THUC_THANH_TOAN",
        "order": 0
      },
      {
        "code": "DM_DON_VI_TINH",
        "order": 0
      },
      {
        "code": "DM_TY_LE_VAT",
        "order": 0
      },
      {
        "code": "DM_DINH_KHOAN",
        "order": 0
      },
      {
        "code": "DM_NOI_XUAT_HOA_DON",
        "order": 0
      },
      {
        "code": "DM_QUOC_GIA",
        "order": 0
      },
      {
        "code": "DM_TAI_KHOAN_NGAN_HANG",
        "order": 0
      },
      {
        "code": "DM_TY_LE_MIEN_GIAM_HOC_PHI",
        "order": 0
      },
      {
        "code": "DM_HINH_THUC_THU",
        "order": 0
      },
      {
        "code": "DM_LOP_HOC",
        "order": 0
      },
    ];
    dataDM = this.listsService.getDMBy(arr);
    if (dataDM.length > 0) {
      dataDM.forEach(element => {
        dataJoin.push(element);
      });
    }
    _countDm = dataDM.length;
    if (isTT78) {
      let dataRecord = this.vatService.getInvoiceResolverTT78(loaiHoaDon);
      if (dataRecord.length > 0) {
        dataRecord.forEach(element => {
          dataJoin.push(element);
        });
      }
      let dataForkJoin = forkJoin(dataJoin);
      return dataForkJoin.pipe(
        take(1),
        map(rs => {
          Library.hideloading();
          let status = sEnum.statusApi.success;
          let isEdit = false;
          for (let index = 0; index < rs.length; index++) {
            if (rs[index].status != sEnum.statusApi.success) {
              Library.notify(rs[index].message, "error");
              if (loaiHoaDon == "01GTKT")
                this.router.navigate(['/system/student-vat-invoice']);
              else if (loaiHoaDon == "02GTTT")
                this.router.navigate(['/system/student-sale-invoice']);
              else if (loaiHoaDon == "03XKNB")
                this.router.navigate(['/system/transport-invoice']);
              status = sEnum.statusApi.error;
              break;
            }
          }
          if (status == sEnum.statusApi.success) {
            if (dataDM.length > 0) {
              let _data = [];
              rs.forEach((element, i) => {
                if (i < dataDM.length) {
                  _data.push(element);
                }
              });
              this.authService.setDMLocal(_data);
            }
            /* Thng t 78 */
            let templates = Helper.convertTemplatesToInvoiceResolver(rs[dataJoin.length - 1]).filter(item => item.template_type !== 'HOA_DON_KHOI_TAO_TU_MAY_TINH_TIEN'&& item.template_type !== 'PHIEU_THU_MAY_TINH_TIEN');
            return [
              rs[_countDm].data.products,
              templates,
              isEdit,
            ];
          }
          else
            return null;
        })
      );
    }
    else {
      let dataRecord = this.vatService.getInvoiceResolver(loaiHoaDon);
      if (dataRecord.length > 0) {
        dataRecord.forEach(element => {
          dataJoin.push(element);
        });
      }
      let dataForkJoin = forkJoin(dataJoin);
      return dataForkJoin.pipe(
        take(1),
        map(rs => {
          Library.hideloading();
          let status = sEnum.statusApi.success;
          let isEdit = false;
          for (let index = 0; index < rs.length; index++) {
            if (rs[index].status != sEnum.statusApi.success) {
              Library.notify(rs[index].message, "error");
              if (loaiHoaDon == "01GTKT")
                this.router.navigate(['/system/student-vat-invoice']);
              else if (loaiHoaDon == "02GTTT")
                this.router.navigate(['/system/student-sale-invoice']);
              else if (loaiHoaDon == "03XKNB")
                this.router.navigate(['/system/transport-invoice']);
              status = sEnum.statusApi.error;
              break;
            }
          }
          if (status == sEnum.statusApi.success) {
            let invoice_releases = Helper.convertDataToInvoiceResolver(rs[_countDm]);
            if (dataDM.length > 0) {
              let _data = [];
              rs.forEach((element, i) => {
                if (i < dataDM.length) {
                  _data.push(element);
                }
              });
              this.authService.setDMLocal(_data);
            }
            return [
              invoice_releases,
              rs[dataJoin.length - 1].data.products,
              isEdit
            ];
          }
          else
            return null;
        })
      );
    }
  }
}