import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router } from '@angular/router';

import { DxMenuComponent } from 'devextreme-angular';
import List from 'devextreme/ui/list';
import { VatService } from '@modules/vat/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Library, Helper, AuthService, VPreviewInvService, SocketService } from '@core/index';
import { ViewAttachFileComponent } from '@shared/modules/vat-shared/components/view-attach-file/view-attach-file.component';
import * as moment from 'moment';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { CustomersService } from '@modules/customers/shared';
import ArrayStore from 'devextreme/data/array_store';
import { Subscription } from 'rxjs';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-invoices-duplicates',
  templateUrl: './invoices-duplicates.component.html',
  styleUrls: ['./invoices-duplicates.component.scss']
})
export class InvoicesDuplicatesComponent implements OnInit {
  loaiHoaDon: string;
  selectedItems: Array<any> = [];
  _language: string;
  _translate: any;
  ganXong = false;
  clickCurent = '';
  disEditMulti = true;
  invoiceList: any;
  invoiceIndex = 0;
  tempKiFile = 0;
  tempTemplate: any;
  showPopup = false;
  totalInvoice: number;
  invoices: any;
  invoice_list: any = [];
  isReceipts: any;
  configs: any;
  dataInfor: any;
  checkSubmit: any;
  ioSignature: Subscription;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private vatService: VatService,
    public translate: TranslateService,
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    private socketService: SocketService,
    private spinner: NgxSpinnerService,
    private customersService: CustomersService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
  }

  ngOnInit() {
    this.convertData();
    this.configs = this.authService.getConfigs();
  }

  convertData() {
    this.invoiceList.forEach(item => {
      item.invoice_products.forEach(element => {
        let invoice = new Object;
        invoice["id"] = item.id;
        invoice["student_id"] = item.student_id;
        invoice["invoice_number"] = item.invoice_number;
        invoice["invoice_date"] = item.invoice_date;
        invoice["class_name"] = item.class_name;
        invoice["student_code"] = item.student_code;
        invoice["student_name"] = item.student_name;
        invoice["product_code"] = element.product_code;
        invoice["school_fee_month"] = element.school_fee_month;
        this.invoice_list.push(invoice);
      });
    });
  }

  onEditorPreparing(e) {

  }

  onEditorPrepared(e) {
    if (e.dataField == 'buyer_email' && e.parentType == 'dataRow') {
      //   e.row.cells[6].text = "htkt";
      //  e.editorOptions.onValueChanged = function(args){
      //    e.row.cells[6].value = args.value;
      //       e.setValue(args.value);
      //  }
    }
  }

  onEditingStart(e) {
    e.component.saveEditData();
  }
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      const data: any = message;
      if (data.SvcName === 'efy_viewInvoice') {
        if (data.Status == "success")
          this.viewInvoice(data.Data.Content, data.Data.typeView, data.Data.hinhthucxuat, data.Data.invoice_date);
        else if (data.Status == "error") {
          const popUp = window.open('about:blank', 'name', 'width=200, height=100');
          if (popUp == null || typeof (popUp) == 'undefined') {
          }
          else
            popUp.close();
          Library.notify(data.Description, 'error');
          this.spinner.hide();
        }
      }
    });
  }
  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  viewInvoice(content, typeView, hinhthucxuat, invoice_date) {
    if (typeView == "XEM_TRUOC") {
      let w = 1920, h = 900;
      w = window.screen.availWidth * 100 / 100;
      h = window.screen.availHeight * 93 / 100;
      const pdf_file = content;
      const objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
      const win = window.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
      win.resizeTo(screen.availWidth, screen.availHeight);
      win.focus();
      this.spinner.hide();
    }
  }
  view(data) {
    if (data) {
      let ids = [];
      if (data) {
        ids.push(data.id);
      }
      let w = 1920, h = 900;
      w = window.screen.availWidth * 100 / 100;
      h = window.screen.availHeight * 93 / 100;
      let win = window.open('about:blank', 'name', 'width=200, height=100');
      win.document.body.innerHTML = this._translate.NOTIFY.loading;
      win.blur();
      Library.showloading();
      if (this.configs.system_parameter.display_template_from_server) {
        let sub = this.vatService.xemHoaDonInvoiceImport(ids).subscribe(rs => {
          if (rs.status == 'success') {
            let pdf_file = rs.data.invoice.pdf_file;
            let objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
            win.resizeTo(screen.availWidth, screen.availHeight);
            win.focus();
            win.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
          } else {
            win.close();
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      } else {
        let popup = window.open('about:blank', 'name', `width=200, height=100`);
        popup.document.body.innerHTML = this._translate.NOTIFY.loading;
        popup.blur();
        this.spinner.show();
        const sub = this.vatService.xemHoaDonInvoiceImport(ids).subscribe(rs => {
          if (rs.status === 'success') {
            /* Xem ha n t Client */
            let json_data = rs.data.invoice.json_data;
            let appCode = "REPX";
            if (rs.data.invoice.is_html_template)
              appCode = "HTML";
            this.socketService.xemhoadon(() => this.connectWebsocket(), json_data, appCode, "XEM_TRUOC");
          } else {
            popup.close();
            this.spinner.hide();
            // this.checkSubmit.close();
            Library.notify(rs.message, 'error');
          }
        }, err => {
          this.spinner.hide();
        }, () => {
          //  Library.hideloading();
          sub.unsubscribe();
        });
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  dong() {
    this.bsModalRef.content.successSaveItem = 'success';
    this.bsModalRef.hide();
  }

  selectionChanged(data: any) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    this.disEditMulti = true;
    this.selectedItems = data.selectedRowsData;
    if (this.selectedItems.length > 0) {
      let checks = this.selectedItems.filter((item) => {
        return item.number_of_vouchers;
      });
      let checkDaXuat = this.selectedItems.filter((item) => {
        return item.status == 'DA_XUAT';
      });
      if (checks.length == this.selectedItems.length && checkDaXuat.length == 0) {
        this.disEditMulti = false;
      }
    }
  }

}

